<template>
  <div class="ver-code">
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">基础设置</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">通用代码</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
          <div class="button-content">
            <Button type="primary"@click="operate('1')" v-authcontrol="{menuCode:'112004004001'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
          </div>
        </div>
        <div class="contentCellBody">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="类别：" prop="type">
                <Input v-model="formValidate.type" placeholder="请输入类别" style="width:200px"></Input>
              </FormItem>
              <FormItem label="代码：" prop="code">
                <Input v-model="formValidate.code" placeholder="请输入代码" style="width:200px"></Input>
              </FormItem>
              <FormItem label="名称：" prop="name">
                <Input v-model="formValidate.name" placeholder="请输入名称" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button
                  type="primary"
                  @click="handleSubmit(0, 1)"
                >查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td>
                    <span class="spanBox" @click="operate('2', item)" v-authcontrol="{menuCode:'112004004002'}">修改</span>
                    <span class="spanBox" @click="operate('3', item)" v-authcontrol="{menuCode:'112004004003'}">删除</span>
                  </td>
                  <td>{{item.type}}</td>
                  <td>{{item.code}}</td>
                  <td>{{item.name}}</td>
                  <td>
                    <el-popover
                          placement="top-start"
                          title
                          width="500"
                          trigger="hover"
                          :content="item.remark"> 
                          <span  slot="reference" class="disBlock textOverflow textMaxWidth" >{{item.remark}}</span>
                        </el-popover>
                  </td>
                  <td>{{item.supCode}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
      <div class="flexCenter pageBox">
        <Page :total="rowCount" 
            :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun'
            :current.sync="currentpage"
            show-elevator show-sizer></Page>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '60%'}"
    >
      <addVerCode  ref="dialogModal" :key="keyId" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%"></addVerCode>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addVerCode from "./addVerCode"; //添加和修改
export default {
  components: {
    addVerCode
  },
  data() {
    return {
      keyId:"",
      showModal: false,
      dataObj:{
        type: "1", //1新增，2修改
        data:[],
      },
      modelTitle: "新增",
      pageSize:10,
      pageNum:1,
      rowCount:0,
      currentpage:1,
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "类别" },
        { name: "代码" },
        { name: "名称" },
        { name: "描述" },
        { name: "父码" }
      ],
      tableBody: [
      ],
      formValidate: {
        code: "", 
        name: "",
        type:""
      },
      ruleValidate: {
        Inputvalue1: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ],
        Inputvalue2: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created(){
    this.handleSubmit(0 ,1);
  },
  methods: {
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo){
      let data = {
        code: this.formValidate.code.trim(),//地区代码
        name: this.formValidate.name.trim() ,//名字
        type: this.formValidate.type.trim() ,//类型
        ifExacte:ifExacte,
      }
      this.axios
      .post(`/system/common/listSearch/${pageNo}/${this.pageSize}`, data, {
          headers: { "Content-Type": "application/json" }
        })
      .then(response => {
        if (response.code == "200") {
          this.tableBody = response.data.records;
          this.rowCount = response.data.total;
        }
      })
      .catch(error => {});
    },
    pageSizefun(pageSize){
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo){
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo)
    },
    deleteItem(item){
      console.log('item', item);
      if(item.id){
        this.axios
        .post(`/system/common/delete/${item.id}`,{
            headers: { "Content-Type": "application/json" }
          })
          .then(response =>{
            this.$Message.success("删除成功！");
            this.handleSubmit(0, 1);
            this.currentpage = 1;
            this.pageNum = 1;
          })
          .catch(error=>{
            console.log(error);
          })
      }
    },
    operate(type, item) {
      console.log('item', item)
      switch (type) {
        case "1":
          this.keyId = !this.keyId;
          this.modelTitle = "新增";
          this.showModal = true;
          this.dataObj = {
            type: 1,
            data: [],
            showFlag:true
          };
          break;
        case "2":
          this.keyId = item.id;
          this.modelTitle = "修改";
          this.dataObj = {
            type: 2,
            data: item,
          };
          this.showModal = true;
          break;
        case "3":
          this.$Modal.confirm({
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
              this.deleteItem(item);
            },
            onCancel: () => {}
          });
          break;
      }
    },
    saveDatas() {
      this.showModal = false;
      this.pageNum = 1;
      this.currentpage = 1;
      this.handleSubmit(0,1);
    },
    cancelDialog(val) {
      this.showModal = val;
      this.cancel();
    },
    cancel(){
      if(this.$refs['dialogModal'].$refs['formValidate']){
        this.$refs['dialogModal'].$refs['formValidate'].resetFields();
      }
    }
  }
};
</script>
<style lang="scss" @scoped>
    .ver-code {
      .rightTopbtn {
          margin: 15px 4px;
          float: right;
      }
    }
</style>



