<template>
  <div class="add-ver-type">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="100"
      class="form-content"
    >
      <FormItem label="类别：" prop="type">
        <Input v-model="formValidate.type" placeholder="请输入类别"></Input>
      </FormItem>
      <FormItem label="代码：" prop="code">
        <Input v-model="formValidate.code" placeholder="请输入代码"></Input>
      </FormItem>
      <FormItem label="名称：" prop="name">
        <Input v-model="formValidate.name" placeholder="请输入名称"></Input>
      </FormItem>
      <FormItem label="描述：" prop="remark">
        <Input
          v-model="formValidate.remark"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入描述"
        ></Input>
      </FormItem>
      <FormItem label="父码：" prop="supCode">
        <Input v-model="formValidate.supCode" placeholder="请输入父码"></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  data() {
    return {
      formValidate: {
        type: "",
        code: "",
        name: "",
        remark: "",
        supCode: "",
        id: "",
      },
      ruleValidate: {
        type: [
          {
            required: true,
            message: "类别不能为空！",
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "代码不能为空！",
            trigger: "blur"
          }
        ],
        name: [
          {
            required: false,
            message: "名称不能为空！",
            trigger: "blur"
          }
        ],
        remark: [
          {
            required: false,
            type: "string",
            max: 512,
            message: "描述不能超过512个字！",
            trigger: "blur"
          }
        ],
        supCode: [
          {
            required: false,
            message: "父码不能为空！",
            trigger: "blur"
          }
        ],
      }
    };
  },
  created() {
    // 1:新增 2修改
    if (this.dataObj.type == "2") {
      let datas = this.dataObj.data;
      if(datas && datas.id){
        this.getInfInfoRow(datas);
      }
    } 
  },
  methods: {
    /**
     * name:保存新增信息
     **/
    handleSubmit(name) {
      console.log(this.formValidate);
      this.$refs[name].validate(valid => {
        if (valid) {
          // 新增
          if(this.dataObj.type == "1"){
            this.axios
              .post("/system/common/add", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$emit("save");
                  this.$Message.success("保存成功！");
                }
              })
              .catch(error => {});
          }else{
            // 修改
            this.axios
              .post("/system/common/update", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.success("修改成功！");
                  this.$emit("save");
                }
              })
              .catch(error => {});
          }
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    },
    getInfInfoRow(datas) {
      this.formValidate = {
        type: datas.type,
        code: datas.code,
        name: datas.name,
        remark: datas.remark,
        supCode: datas.supCode,
        id: datas.id,
      }
    },
  }
};
</script>
<style lang="scss" @scoped>
.add-ver-type {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


